import React, {useState, useEffect, useRef} from 'react';
import "./style/css/main.css";
import OnlyLearn from "./images/onlylearn.jpg";
import Dashboard from "./images/dashboard.jpg";
import { Power3, TweenLite, gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

function Main() {

  const [status, setStatus] = useState("View email");
  const [toggle, setToggle] = useState<null | boolean>(null);
  const [mobile, setMobile] = useState<boolean | null>(false);


  // animations
  let header: any = useRef(null);
  let image:any = useRef(null);
  let logo:any = useRef(null);
  let button:any = useRef(null);
  let scroll: any = useRef(null);

  let l1: any = useRef(null);
  let l2: any = useRef(null);
  let l3: any = useRef(null);
  let l4: any = useRef(null);

  let main: any = useRef(null);

  let work: any = useRef(null);
  let card1: any = useRef(null);
  let card2: any = useRef(null);
  
  let paragraph: any = useRef(null);

  let skills: any = useRef(null);
  let box1: any = useRef(null);
 

  let about: any = useRef(null);
  let part1: any = useRef(null);
  let part2: any = useRef(null);

  let hire: any = useRef(null);
  let hireBox: any = useRef(null);


  useEffect(() => {
    TweenLite.to(main, 0, { css: {visibility: "visible"}})

    TweenLite.from( logo, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: 0.2});
    TweenLite.from( l1, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: .5});
    TweenLite.from( l2, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: .6});
    TweenLite.from( l3, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: .7});
    TweenLite.from( l4, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: .8});
    TweenLite.from( image, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: .8});
    TweenLite.from( header, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: 1});
    TweenLite.from( button, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: 1.1});
    TweenLite.from( scroll, .7, {opacity: 0, y: 30, ease: Power3.easeOut, delay: 1.2});

    gsap.fromTo(work, {autoAlpha: 0}, {duration: .9, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: work,
      toggleActions: "play none none reverse",
 
    }});

    gsap.fromTo(card1, {autoAlpha: 0}, {duration: .8, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: card1,
      toggleActions: "play none none reverse",
  
    }});

    gsap.fromTo(card2, {autoAlpha: 0}, {duration: .8, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: card2,
      toggleActions: "play none none reverse",

    }});


    gsap.fromTo(paragraph, {autoAlpha: 0}, {duration: .8, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: paragraph,
      toggleActions: "play none none reverse",
    }});

    gsap.fromTo(skills, {autoAlpha: 0}, {duration: .8, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: skills,
      toggleActions: "play none none reverse", 
    }})

    gsap.fromTo(box1, {autoAlpha: 0}, {duration: .7, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: box1,
      toggleActions: "play none none reverse",
    }})

    gsap.fromTo(about, {autoAlpha: 0}, {duration: .8, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: about,
      toggleActions: "play none none reverse", 
    }})

    gsap.fromTo(part1, {autoAlpha: 0}, {duration: .7, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: part1,
      toggleActions: "play none none reverse",
    }})

    gsap.fromTo(part2, {autoAlpha: 0}, {duration: .7, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: part2,
      toggleActions: "play none none reverse",
    }})

    
    gsap.fromTo(hire, {autoAlpha: 0}, {duration: .7, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: hire,
      toggleActions: "play none none reverse",
    }})

    gsap.fromTo(hireBox, {autoAlpha: 0}, {duration: .7, autoAlpha: 1, ease: Power3.easeIn, scrollTrigger:{
      trigger: hireBox,
      toggleActions: "play none none reverse",
    }})

  }, [])

  // little trick to fix a bug

  useEffect(() => {
    setToggle(true)
  }, [])

  const handleView = () => {
    setToggle(!toggle)

    const email = document.querySelector("#email") as HTMLParagraphElement;
    if(toggle){
      email.style.opacity = "1";
      setStatus("Hide email")
    
    }

    if(!toggle){
      email.style.opacity = "0";
      setStatus("View email")
    
    }
  }
  
  const list = document.querySelector(".links") as HTMLUListElement;
  const line1 = document.querySelector(".line1") as HTMLDivElement;
  const line2 = document.querySelector(".line2") as HTMLDivElement;
  const line3 = document.querySelector(".line3") as HTMLDivElement;
  const addon = document.querySelector(".addon") as HTMLDivElement;
  const links = document.querySelectorAll(".link")!;
  const hamburger = document.querySelector(".hamburger") as HTMLDivElement;

  const handleMobile = () => {
    setMobile(!mobile);

    if(mobile){
      addon.style.zIndex = "123";
      addon.style.opacity = ".47";

      list.classList.add("mobileUL");

      links.forEach((t) => {
        t.classList.add("mobileLink");
      })

      hamburger.classList.add("active");

      line1.style.backgroundColor = "black";
      line3.style.backgroundColor = "black";

      line2.style.transform = "translateX(-70px)"
      line2.style.opacity = "0";

      line1.style.transform = "rotate(-45deg)";
      line3.style.transform = "rotate(45deg)";
      line3.style.marginTop = "-13px";

    }else{
      hamburger.classList.remove("active");
     
      line1.style.backgroundColor = "white";
      line3.style.backgroundColor = "white";

      line2.style.transform = "translateX(0)";
      line2.style.opacity = "1";

      line1.style.transform = "rotate(0deg)";

      line3.style.transform = "rotate(0deg)";
      line3.style.marginTop = "7px";

      list.classList.remove("mobileUL");
      links.forEach((t) => {
        t.classList.remove("mobileLink");
      })

      addon.style.zIndex = "-1";
      addon.style.opacity = "0";
    }
  }

  const handleExit = () => {
    setMobile(!mobile)

    list.classList.remove("mobileUL");
    links.forEach((t) => {
      t.classList.remove("mobileLink");
    })

    addon.style.zIndex = "-1";
    addon.style.opacity = "0";

    hamburger.classList.remove("active");
     
    line1.style.backgroundColor = "white";
    line3.style.backgroundColor = "white";

    line2.style.transform = "translateX(0)";
    line2.style.opacity = "1";

    line1.style.transform = "rotate(0deg)";

    line3.style.transform = "rotate(0deg)";
    line3.style.marginTop = "7px";
    
  }

  return (
    <>
      <div className="addon" onClick={handleExit}></div>
      <div className="main" ref={m => main = m}>
        <div className="nav">
            <div ref={(l) => logo = l}>
                <a href="/" className="logo" >Tudor Alexandru</a>
            </div>
                
            <ul className="links" >
                <a href="#work" className="link" ><li ref={l => l1 = l}>My work</li></a>
                <a href="#skills" className="link" ><li ref={b => l2 = b}>My skills</li></a>
                <a href="#about" className="link" ><li ref={b => l3 = b}>About me</li></a>
                <a href="#hire" className="link" ><li ref={b => l4 = b}>Hire me</li></a>
            </ul>


            <div className="hamburger" onClick={handleMobile}>
              <div className="line line1"></div>
              <div className="line line2"></div>
              <div className="line line3"></div>
            </div>
        </div>
        
        <div className="hero">
            <h1 ref={(h) => header = h}>I create beautiful websites with my hands</h1>
            <a href="mailto:tudorale2005@gmail.com">
              <button ref={b => button = b}> 
                  <svg xmlns="http://www.w3.org/2000/svg" className="mail" width="44" height="44" viewBox="0 0 24 24" strokeWidth="1.5" stroke="#6f32be" fill="none" strokeLinecap="round" strokeLinejoin="round">
                      <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                      <line x1="10" y1="14" x2="21" y2="3" />
                      <path d="M21 3l-6.5 18a0.55 .55 0 0 1 -1 0l-3.5 -7l-7 -3.5a0.55 .55 0 0 1 0 -1l18 -6.5" />
                  </svg>
                      <p>Contact me</p>
              </button>
            </a>
               
            <svg ref={s => scroll = s} className="scroll" width="40" height="77" viewBox="0 0 40 77">
                <g id="scrollCircle" transform="translate(-253 -787)">
                  <g id="Rectangle_12"  transform="translate(253 787)" fill="none" stroke="#fff" strokeWidth="4">
                    <rect width="40" height="77" rx="20" stroke="none"></rect>
                    <rect x="2" y="2" width="36" height="73" rx="18" fill="none"></rect>
                  </g>
                  <circle className="circle" id="Ellipse_1" cx="11" cy="11" r="11" transform="translate(262 798)" fill="#fff"></circle>
                </g>
              </svg>
              


       
       {/* keyboard ilustration (rights are reserved)*/}

          <svg ref={(i) => image = i}viewBox="7.426 -6.6 495.049 207.92" className="keyboard" xmlns="http://www.w3.org/2000/svg">
            <rect style={{fill: "rgb(74, 23, 155)"}}  x="24.166" y="2.917" width="467.821" height="179.868" rx="11" ry="11"/>
            <rect style={{fill: "rgb(232, 232, 232)"}} className="wrapper" x="16.914" y="10.634" width="467.821" height="179.868" rx="11" ry="11"/>

            <rect style={{fill: "rgb(45, 138, 204)"}}  className="key keyAnimation" x="29.289" y="23.841" width="40.429" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="94.359" y="23.841" width="48.463" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="153.986" y="23.841" width="48.463" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="213.923" y="24.723" width="48.463" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="273.957" y="25.783" width="48.463" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="334.358" y="25.783" width="48.463" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="395.346" y="25.665" width="70.957" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="28.399" y="64.531" width="84.983" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="123.471" y="64.531" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="182.526" y="65.413" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="242.597" y="66.473" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "rgb(45, 138, 204)"}} className="key keyAnimation" x="301.87" y="66.473" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="147.882" y="93.554" width="47.735" height="27.228" rx="5" ry="5" transform="matrix(0.999979, 0.006511, 0, 1.000021, 271.143616, -27.330996)"/>
            <rect style={{fill: "white"}} className="key" x="360.899" y="66.802" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "rgb(45, 138, 204)"}} className="key keyAnimation" x="151.595" y="107.629" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="211.666" y="107.689" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="270.939" y="107.689" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="245.016" y="93.554" width="79.089" height="27.228" rx="5" ry="5" transform="matrix(0.999979, 0.006511, 0, 1.000021, 143.081161, 13.252908)"/>
            <rect style={{fill: "white"}}className="key"  x="329.968" y="108.018" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="30.034" y="105.931" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="89.615" y="106.931" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="151.338" y="149.539" width="191.232" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="29.777" y="149.841" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="89.358" y="149.841" width="48.493" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "white"}} className="key" x="352.166" y="149.01" width="50.968" height="27.228" rx="5" ry="5"/>
            <rect style={{fill: "rgb(45, 138, 204)"}} className="key keyAnimation" x="413.272" y="148.835" width="52.618" height="27.228" rx="5" ry="5"/>

          

        </svg>

        </div>
       
        <h1 id="work" ref={w => work = w}>My work</h1>

        <div className="portfolio">

          <div className="card card1" ref={i => card1 = i}>

            <div className="info" >
              <h1 className="info_title">ONLY LEARN</h1>
              <p className="info_content">
                 This is my latest project and the biggest one, finished in December 2020, it is a learning platform with over 60 lessons in HTML5 and CSS3 and over 70 pages in total with authentication and text editor system.
              </p>
              <h1 className="info_tech">Technologies</h1>

              <div className="technologies">
                <div className="tech">React JS</div>
                <div className="tech">Firebase</div>
                <div className="tech">CSS3</div>
              </div>
              
              <div className="buttons">
                <a href="https://only-learn-3ff6b.web.app/" target="_blank" rel="noreferrer">
                  <button style={{marginLeft: "0px"}}>Website</button>
                </a>
              </div>
            </div>

            <div className="image" >
              <img  src={OnlyLearn} alt="" className="card_image" />
              </div>
          </div>

          <div className="card card2" ref={i => card2 = i}>

            <div className="info" >
                <h1 className="info_title">DASHBOARD</h1>
                <p className="info_content">
                This was the second project in React JS that was finished on August 30 2020, it is a dashboard  for a non-existent company. You can enter as a guest or as an admin to have a little more access. The password and username are both 'admin'.                </p>
                <h1 className="info_tech">Technologies</h1>

                <div className="technologies">
                  <div className="tech">React JS</div>
                  <div className="tech">CSS3</div>
                </div>
                
                <div className="buttons">
                  <a href="https://tudorale.github.io/dashboard/#/" target="_blank" rel="noreferrer">
                    <button  style={{marginLeft: "0px"}}>Website</button>
                  </a>
                  <a href="https://github.com/tudorale/dashboard" target="_blank" rel="noreferrer">
                    <button>Github</button>
                  </a>
                </div>
              </div>

              <div className="image" >
                <img  src={Dashboard} alt="" className="card_image" />
              </div>
          </div>

          <p className="more" ref={p => paragraph = p}><span>*</span> You can find more open source projects on my <a href="https://github.com/tudorale" target="_blank" rel="noreferrer">Github</a></p>
        </div>

        <h1 id="skills" ref={r => skills = r}>My skills</h1>

        <div className="skills" ref={r => box1 = r}>
          <div className="frontend">
            <h1>Front-End Development</h1>
            <p>
            In the Front-End Development field I have more than a year and a half of experience in the modern technologies that a developer is using, like React JS, TypeScript, JavaScript and more.
            </p>
          </div>
          <div className="design" >
            <h1>UI/UX Design</h1>
            <p>I love to design things, from banners to websites and more. I used Adobe Photoshop for more than 3 years and recently I started using Gravit Designer for desining websites.</p>
          </div>
        </div>
        
        <h1 id="about" ref={r => about = r}>About me</h1>

        <p className="part1" ref={r => part1 = r}>First of all, my name is Tudor Alexandru - Ionut and I am from România, my passion for Web Development started in August 2019. I started with HTML5 and I didn't know so much about this, but I chose the Front-End field because I want to show the world what I can do with a computer. </p>
        <div className="part2" ref={r => part2 = r}>
          <div className="content">
              <p>After a few months I knew that I am good at what I'm doing, actually, from the first page that I made at that time I knew that I can do much better. I wasn't like others at start, put some text and add a color and that's it, even though I didn't had the knowledge to make something bigger, like animations, and I don't like to lie myself about my work, I want to make the things perfectly and I think I have a special eye for details.</p>
          </div>
        </div>

        <h1 id="hire" ref={r => hire = r}>Hire me</h1>

        <div className="hire"  ref={r => hireBox = r}>
          <div className="innerHire">
            <p>Do you have an idea that will make you a millionare but you need a website? Or you have a company that needs another man in the team? <span>I'm here!</span></p>

             <a href="mailto:tudorale2005@gmail.com">
              <button> 
                  <svg xmlns="http://www.w3.org/2000/svg" className="mail" width="44" height="44" viewBox="0 0 24 24" strokeWidth="1.5" stroke="#6f32be" fill="none" strokeLinecap="round" strokeLinejoin="round">
                      <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                      <line x1="10" y1="14" x2="21" y2="3" />
                      <path d="M21 3l-6.5 18a0.55 .55 0 0 1 -1 0l-3.5 -7l-7 -3.5a0.55 .55 0 0 1 0 -1l18 -6.5" />
                  </svg>
                      <p>Send an email</p>
              </button>
            </a>
            <div className="emailDiv">
              <p onClick={handleView}>{status}</p>
              <p id="email">tudorale2005@gmail.com</p>
            </div>
          </div>

        <div className="footer">
          <p>Ⓒ 2021 Tudor Alexandru</p>
          <p>Made with love in România</p>
          <div className="icons">

            <a href="https://github.com/tudorale" target="_blank" rel="noreferrer">
              <svg xmlns="http://www.w3.org/2000/svg" className="github" width="44" height="44" viewBox="0 0 24 24" strokeWidth="1.5" stroke="#6f32be" fill="none" strokeLinecap="round" strokeLinejoin="round">
                <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                <path d="M9 19c-4.3 1.4 -4.3 -2.5 -6 -3m12 5v-3.5c0 -1 .1 -1.4 -.5 -2c2.8 -.3 5.5 -1.4 5.5 -6a4.6 4.6 0 0 0 -1.3 -3.2a4.2 4.2 0 0 0 -.1 -3.2s-1.1 -.3 -3.5 1.3a12.3 12.3 0 0 0 -6.2 0c-2.4 -1.6 -3.5 -1.3 -3.5 -1.3a4.2 4.2 0 0 0 -.1 3.2a4.6 4.6 0 0 0 -1.3 3.2c0 4.6 2.7 5.7 5.5 6c-.6 .6 -.6 1.2 -.5 2v3.5" />
              </svg>
            </a>

            <a href="https://www.linkedin.com/in/tudor-alexandru-a503321a3/" target="_blank" rel="noreferrer"> 
              <svg xmlns="http://www.w3.org/2000/svg" className="linkedin" width="44" height="44" viewBox="0 0 24 24" strokeWidth="1.5" stroke="#6f32be" fill="none" strokeLinecap="round" strokeLinejoin="round">
                  <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                  <rect x="4" y="4" width="16" height="16" rx="2" />
                  <line x1="8" y1="11" x2="8" y2="16" />
                  <line x1="8" y1="8" x2="8" y2="8.01" />
                  <line x1="12" y1="16" x2="12" y2="11" />
                  <path d="M16 16v-3a2 2 0 0 0 -4 0" />
              </svg>
            </a>

            <a href="mailto:tudorale2005@gmail.com">
              <svg xmlns="http://www.w3.org/2000/svg" className="email" width="44" height="44" viewBox="0 0 24 24" strokeWidth="1.5" stroke="#6f32be" fill="none" strokeLinecap="round" strokeLinejoin="round">
                <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                <line x1="10" y1="14" x2="21" y2="3" />
                <path d="M21 3l-6.5 18a0.55 .55 0 0 1 -1 0l-3.5 -7l-7 -3.5a0.55 .55 0 0 1 0 -1l18 -6.5" />
              </svg>
            </a>
          </div>
        </div>

        </div>
    </div>
    </>
  );
}

export default Main;
